----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

.inner-body {
  position: relative;
  width: 100%;
}

h1,
.rubric.h1 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 34px;
  font-weight: 800;
  letter-spacing: .03em;
}

h2,
.rubric.h2 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 28px;
  font-weight: 800;
  letter-spacing: .03em;
}

h3,
.rubric.h3 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 22px;
  font-weight: 800;
  letter-spacing: .03em;
}

h4,
.rubric.h4 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 18px;
  font-weight: 800;
  letter-spacing: .03em;
}

h5,
.rubric.h5 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 800;
  letter-spacing: .03em;
}

h6,
.rubric.h6 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 800;
  letter-spacing: .03em;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a:active {
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* Light theme -------------------------------------------------------------- */
body {
  background-color: #fff;
  color: #292929;
}

body a {
