/* -----------------------------------------------------------------------------
  Component: Breadcrumbs

  Style for the breadcrumbs present in the content area.
----------------------------------------------------------------------------- */
#breadcrumbs {
  position: fixed;
  top: 77px;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-end;
  width: calc(100% - 385px - 290px);
  height: 60px;
  padding: 5px 15px 5px 30px;
  margin: 0;
  list-style: none;
  z-index: 99;
}

.breadcrumb-separator::after {
  display: inline-block;
  margin: 0 7px;
  content: "/";
  font-size: 1.1rem;
}

/* Light theme -------------------------------------------------------------- */
body #breadcrumbs {
  color: #292929;
  background-color: #fff;
}

